@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
body {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  background-color: #121212;
}
.sign__logo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #f590b2;
  margin-bottom: 14px;
}
h1 {
  color: white;
  font-weight: 700;
  font-size: 28px;
  margin-bottom: 36px;
}
h3 {
  color: white;
  font-weight: 400;
  font-size: 22px;
  margin-bottom: 28px;
}
.links__container {
  margin-top: 22px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.link {
  text-decoration: none;
  font-size: 14px;
  color: #8fc9f7;
  transition: all 0.3s ease-in-out;
}
.link:visited {
  color: #8fc9f7;
}
.link:hover {
  cursor: pointer;
  color: white;
}
.copyright {
  margin-top: 60px;
  font-size: 14px;
  color: #b8b8b8;
}
.input {
    border: 1px solid #494949;
    border-radius: 4px;
    background-color: transparent;
    padding: 14px 10px;
    font-size: 16px;
    color: white;
    margin-bottom: 22px;
    outline: none;
}
.input:hover, .input:focus {
    border: 1px solid white;
}
.input__email, .input__password {
    width: 100%;
}
.input__text {
    width: 47.5%;
}
.input__checked, .input__checked:hover, .input__checked:focus {
    border: 1px solid #8fc9f7;
}
.input__unchecked, .input__unchecked:hover, .input__unchecked:focus {
    border: 1px solid #f590b2;
}
.signin {
    position: relative;
    width: 360px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.error__container__signin {
    position: absolute;
    bottom: -50px;
}
.error__signin {
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    color: #f590b2;
}
.checkbox__container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}
#checkbox {
    margin-right: 14px;
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.checkbox__label {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: white;
}
.checkbox__label::before {
    margin-right: 14px;
    content: "";
    display: inline-block;
    width: 18px;
    height: 18px;
    min-width: 18px;
    border: 2px solid #b8b8b8;
    border-radius: 1px;
}
#checkbox:checked + .checkbox__label::before {
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: contain;
    border: 2px solid white;
    box-shadow: 0px 0px 2px 1px #b8b8b8;
}
#checkbox:hover + .checkbox__label::before {
    box-shadow: 0px 0px 2px 1px #b8b8b8;
}
.main__button {
    font-weight: 500;
    padding: 8px 0;
    font-size: 14px;
    margin-top: 36px;
    border-radius: 4px;
    text-transform: uppercase;
    width: 100%;
    background-color: #8fc9f7;
    border: 1px solid #8fc9f7;
    transition: all 0.3s ease-in-out;
}
.main__button:hover {
    cursor: pointer;
    color: #8fc9f7;
    background-color: transparent;
}
.main__button:active {
    box-shadow: 0 0 5px 5px #8fc9f7;
    transition: 0s;
}
.signup {
    position: relative;
    width: 360px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.name__container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.links__signup {
    -webkit-justify-content: right;
            justify-content: right;
}
.error__container__signup {
    position: absolute;
    bottom: -50px;
}
.error__signup {
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    color: #f590b2;
}
